import React from "react"
import { DomainsResultsStyled } from "./DomainsResults.styled"
import { Link } from "gatsby"
import Tlds from "./Tlds"
import Doms from "./Doms"
import { ShowLinkStyled, ColorsStyled, _, __, ___ } from "./Domains.styled"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
import Search from "../../components/Search"
import { faStar as faStarSolid, faBolt } from "@fortawesome/pro-solid-svg-icons"
import { faTimes, faCode } from "@fortawesome/pro-regular-svg-icons"
import WordPoss from "../../components/WordPoss"
import OptionsMore from "./OptionsMore"
import { message } from "antd"
import Options from "./Options"
import { DomsStyled } from "./Doms.styled"

function get_ss(key, default_value) {
  // return default_value
  let ss_value = window.sessionStorage.getItem(key)
  // console.log('got from ss:', key, ss_value);
  return ss_value ? JSON.parse(ss_value) : default_value
}

function set_ss(key, value) {
  window.sessionStorage.setItem(key, JSON.stringify(value))
}

const DomainsResults = function (props) {
  /*
   * Welcome
   */
  if (!get_ss("welcomed")) {
    message.warn(
      <span className="color-medium" style={{ verticalAlign: "-0.125rem" }}>
        <b className="nowrap">
          &nbsp;Welcome to our <span className="color-attention">beta</span> preview! &nbsp;
        </b>
        <span className="nowrap">You are one of the first to see this&nbsp;</span>
        <b className="nowrap">&thinsp;proof of concept&thinsp;</b>.
      </span>,
      10
    )
    set_ss("welcomed", true)
  }

  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  /*
   * View variables:
   */
  // for results:
  let {
    suggestions_availability,
    input_str,
    input_tld,
    input_is_name,
    input_is_brand,
    input_is_tech,
    input_spellchecked
  } = that.props
  input_str = input_str.toLowerCase()
  let cue = []
  let original_domain = input_str.replace(/ /g, "") + "." + input_tld
  let original_code = suggestions_availability[original_domain] || 0
  /*
   * message about availability
   */
  if (!original_code) {
    cue.push(<span key="avai">Checking availability...</span>)
  } else if (original_code === 8) {
    cue.push(
      <span key="avai" className="color-white">
        We own "{original_domain}".
      </span>
    )
  } else if (original_code < 10) {
    cue.push(
      <span key="avai" className="color-white">
        "{original_domain}" is not available...
      </span>
    )
  } else if (original_code > 1000) {
    cue.push(
      <b key="avai" className="color-attention">
        "{original_domain}" is on sale for ${Math.round(original_code).toLocaleString()}
      </b>
    )
  } else if (original_code > 100000000) {
    cue.push(
      <span key="avai" className="color-white">
        "{original_domain}" will expire on <b>{(new Date(original_code * 1000) + "").substring(4, 15)}</b>
      </span>
    )
  } else if (original_code >= 10) {
    cue.push(
      <b key="avai" className="color-attention">
        <FA key={1} icon={faStarSolid} className="faStar" /> <b>{original_domain}</b> is available!
      </b>
    )
  } else {
    cue.push(
      <span key="avai">
        "{original_domain}" availability is "{original_code}"!
      </span>
    )
  }
  /*
   * message about spell check
   */
  if (input_spellchecked && input_spellchecked.replace(/ /g, "") !== input_str.replace(/ /g, "")) {
    cue.push(
      <div key="spellchecked" className="spellchecked">
        Including suggestions for "
        <b className="color-white">
          {input_spellchecked.split(" ").map((str, si) => (
            <span key={si}>
              {str}
              <__ />
            </span>
          ))}
          .{input_tld}
        </b>
        ".
        {/*<br /> To avoid spell-checking any word or phrase, put it in "quotes".*/}
      </div>
    )
  } else if (input_is_name) {
    cue.push(
      <div key="spellchecked" className="spellchecked">
        <span className="link nowrap" onClick={that.openContact}>
          Did you mention a person's name?
        </span>{" "}
        We're still improving handling of non-dictionary words.
      </div>
    )
    // } else if (input_is_brand) {
    //   cue.push(
    //     <div key="spellchecked" className="spellchecked">
    //       <span className="link nowrap" onClick={that.openContact}>
    //         Did you mention a brand name?
    //       </span>{" "}
    //       We're still improving results for non-dictionary words.
    //     </div>
    //   )
    // } else if (input_is_tech) {
    //   cue.push(
    //     <div key="spellchecked" className="spellchecked">
    //       <span className="link nowrap" onClick={that.openContact}>
    //         Did you mean to type in a technology term?
    //       </span>{" "}
    //       We're working on auto-categorizing user input.
    //     </div>
    //   )
  } else if (input_str.length > 20 || that.props.str_possibly_corrupted) {
    cue.push(
      <div key="spellchecked" className="spellchecked">
        For best results, add a space between words.
      </div>
    )
  }
  /*
   * show message
   */
  let cue_nav = (
    <ShowLinkStyled
      className="showThesaurus hide-small"
      onClick={() => {
        that.props.ui_actions.RX__toggle_poss()
      }}
    >
      {/*<span className="show-large">powered by:&nbsp; </span>*/}
      {/*<FA icon={faBook} className="faBook color-accent" style={{ transform: "scale(0.95)", opacity: "0.95" }} />*/}
      {/*&nbsp;&thinsp;*/}
      {!!that.props.show_poss ? (
        <>
          <FA icon={faTimes} className="toggleNLPThesaurus faBolt color-accent x145" />
          <span className="color-accent">&nbsp;HIDE thesaurus</span>
          {/*<br />*/}
          {/*<a href="https://nlpthesaurus.com?str=hello" target="_blank">*/}
          {/*  visit nlp*/}
          {/*  <_ />*/}
          {/*  thesaurus*/}
          {/*  <_ />*/}
          {/*  .com &raquo;*/}
          {/*</a>*/}
        </>
      ) : (
        <>
          thesaurus&nbsp;&nbsp;
          <FA icon={faBolt} className="toggleNLPThesaurus faBolt color-accent" />
        </>
      )}
    </ShowLinkStyled>
  )

  return (
    <DomainsResultsStyled>
      {/*<div className="hide-small content center">*/}
      {/*  <p>*/}
      {/*    <div className="absolute-right color-accent">*/}
      {/*      <FA icon={faTimes} className="x145" />*/}
      {/*    </div>*/}
      {/*    Welcome to our beta release! Please <Link to="/contact">let us know what you think</Link>.&nbsp;*/}
      {/*    <span className="show-large">Thank you.</span>*/}
      {/*  </p>*/}
      {/*</div>*/}
      <Search
        {...that.props}
        className={"Search DomainsResults"}
        domains={true}
        placeholder={"enter two or three words"}
        // title="Find an available domain:"
        title_nav={
          <Link to="/api" className="hide-small">
            <span className="color-subtle">use our data &thinsp;</span>
            <FA icon={faCode} className="x115" />
            <___ />
          </Link>
        }
        cue={cue}
        cue_nav={cue_nav}
      />

      {/*
       * POSS content, below Search form
       */}
      {!!that.props.show_poss && <WordPoss {...that.props} />}

      {/*
       * Options closed
       */}
      <Options key="options" that={that} />

      {/*
       * Options opened
       */}
      <OptionsMore that={that} />

      {/*
       * Results
       */}
      <div className="container">
        {/*
         * Suggestions + TLDs + WIP
         */}
        <ColorsStyled
          className={"content results " + (!that.state.gotAvailability ? " gettingAvailability" : " gotAvailability")}
        >
          {/*
           * Results
           */}
          <Doms that={that} />
          <Tlds that={that} />
        </ColorsStyled>
      </div>
    </DomainsResultsStyled>
  )
}

export default DomainsResults
