import React from "react"
import PropTypes from "prop-types"
import { faCaretDown, faCaretUp } from "@fortawesome/pro-solid-svg-icons"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
import { DomsStyled } from "./Doms.styled"
import Dom from "./Dom"
import { H5Styled } from "./Domains.styled"
import { astrings_shuffle_last, astrings_shuffle_last_strict } from "universal-common-scripts/functions/astrings"
import { str_capitalize } from "universal-common-scripts/functions/strings"

let list_title = function (listname, only_available) {
  // custom capitalize / edit title
  if (listname === "tld") {
    if (only_available) {
      return "Available TLDs"
    } else {
      return "TLD Suggestions"
    }
  } else if (listname === "name") {
    if (only_available) {
      return "Available Names"
    } else {
      return "Name Suggestions"
    }
  } else if (listname === "com") {
    if (only_available) {
      return "Available .com"
    } else {
      return ".com"
    }
  }
  // capitalize all by default
  return str_capitalize(listname) + "s"
}
let list_limit = function (listname, show_one_list) {
  // defaults for each category
  // Note: (show_one_list ? 6 : 9)
  // First "?" number is how many to show all at once on top
  // Second ":" number is how many to scatter in ever other few
  let limit = 3
  if (listname === "com") {
    limit = show_one_list ? 1 : 3
  } else if (listname === "tld") {
    limit = show_one_list ? 1 : 6
  } else if (listname === "name") {
    limit = 99
  } else if (listname === "word hack") {
    limit = show_one_list ? 0 : 3
  } else if (listname === "phrase hack") {
    limit = show_one_list ? 0 : 3
  } else {
    limit = show_one_list ? 0 : 9
  }
  // show all available
  // limit = Math.max(limit, available)
  // adjust to screen size
  if (typeof window !== "undefined") {
    /*
     * adjust to screen size
     */
    // 3 columns
    if (window.innerWidth > 1299) {
      if (limit % 3) {
        limit += 3 - (limit % 3)
      }
    }
    // 2 columns
    else if (window.innerWidth > 899) {
      if (limit === 9) {
        limit = 6
      } else if (limit % 2) {
        limit += 2 - (limit % 2)
      }
    }
    // 1 column
    else {
      if (limit === 9) {
        limit = 3
      }
    }
  }
  // done
  return limit
}

class Doms extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      // ['unlimit_'+listname]: {boolean}
      hide_unchecked: false,
      suggestions_domains: props.that.suggestions_domains
    }
  }

  componentDidUpdate() {
    let that = this.props.that
    if (that.props.suggestions_domains !== this.state.suggestions_domains) {
      this.setState({
        hide_unchecked: false,
        suggestions_domains: that.props.suggestions_domains
      })
    }
  }

  render() {
    /*
     * User "container" state/props/methods!
     * This "child" component is to simplify container's render() logic.
     */
    let that = this.props.that
    let { show_one_list, suggestions_domains, suggestions_availability, tlds_user, tlds_checked } = that.props

    /*
     * validate
     */
    if (!suggestions_domains || !Object.keys(suggestions_domains).length) {
      return (
        <DomsStyled className="DomsStyled">
          <div className="content">
            <p>&nbsp;</p>
            <p>loading results......</p>
          </div>
        </DomsStyled>
      )
    }
    let ListNames = []
    let Lists = []
    let doms_displayed = 0

    /*
     * filter each list
     */
    let render_lists = ["tld", "com", "word hack", "phrase hack", "name"]
    for (let listname of render_lists) {
      if (listname === "word hack" && !that.props.show_word_hacks) {
        continue
      }
      if (listname === "phrase hack" && !that.props.show_phrase_hacks) {
        continue
      }
      let list = suggestions_domains[listname]
      if (!list || !list.length) continue
      let List = []
      let tlds_ratings = {}
      let tlds_checked_arr = Object.keys(tlds_checked)
      /*
       * List
       * for each item (phrase string) in list (category of suggestions)
       * fill appropriate list with phrase strings:
       */
      for (let i = 0; i < list.length; i++) {
        let phraseString = list[i]
        let code = Number(suggestions_availability[phraseString.replace(/ /g, "")] || 0)
        /*
         * Rate
         */
        let tld = phraseString.substr(phraseString.indexOf(".") + 1)
        //
        if (tlds_user[tld]) {
          // user-specified tld is best (give highest score)
          tlds_ratings[phraseString] = 1000
        } else {
          // inverse of tld position in tld suggestions (higher number = better)
          let tld_arr_i = tlds_checked_arr.indexOf(tld) + 1
          if (tld_arr_i) {
            // exponential curve: prefer first tld much more than second, much more than third
            let rating = tlds_checked_arr.length - tld_arr_i
            tlds_ratings[phraseString] = rating * rating
          } else {
            // not found (domain hack = lowest score)
            tlds_ratings[phraseString] = 0
          }
        }
        /*
         * Show domain
         */
        if (code === 9 || (code >= 10 && code <= 1000)) {
          // available
          List.push(phraseString)
          doms_displayed++
        } else if (code > 1000 && !!that.props.show_unavailable) {
          // premium
          List.push(phraseString)
          doms_displayed++
        } else if (code < 9 && !!that.props.show_unavailable) {
          // unavailable
          List.push(phraseString)
          doms_displayed++
        } else if (code === 0) {
          // unavailable
          List.push(phraseString)
          doms_displayed++
        }
        /*
         * Enough
         */
        if (listname !== "name" && doms_displayed >= 50) {
          break
        } else if (listname === "name" && doms_displayed >= 150) {
          break
        }
      }
      /*
       * save list content
       */
      Lists.push(List)
      ListNames.push(listname)
    }

    /*
     * List carry-over
     * When first (original phrase) list is limited,
     * some good results maybe hidden. I'd like to feature these results,
     * so, just move them to the next list, but to the middle of it!
     */
    let ListsCarryover = {}
    let ListTop = []

    /*
     * View
     */
    return (
      <DomsStyled className={"DomsStyled " + (!that.props.show_unavailable ? "all_results" : "")}>
        {!that.props.hide_premium && (
          <H5Styled className="doms_title">Still working on premium domains. More results coming soon!</H5Styled>
        )}
        {/*
         * Results
         */}
        {Lists.map((List, li) => {
          let listname = ListNames[li]
          let limit = list_limit(listname, show_one_list)
          let label = list_title(listname, !!that.props.show_unavailable)

          /*
           * Shuffle
           */
          if (show_one_list) {
            List = astrings_shuffle_last_strict(List)
          } else if (listname === "name") {
            List = astrings_shuffle_last_strict(List)
          } else if (listname === "word hack") {
            List = astrings_shuffle_last_strict(List)
          }

          /*
           * Limit number of results
           * Show full list if user clicked to expand it,
           * or limit the list items to maximum number.
           */
          if (this.state["unlimit_" + listname]) {
            limit = 1000
          }
          let ListVisible = []
          if (listname !== "name") {
            /*
             * One combined list
             */
            if (show_one_list) {
              /*
               * Individual lists get carried over completely
               */
              ListTop = [...ListTop, ...List.slice(0, limit)]
              ListsCarryover[listname] = List.slice(limit)
            } else {
              /*
               * Individual lists
               */
              {
                /*
                 * Show some, carry over the rest
                 */
                ListVisible = [...ListVisible, ...List.slice(0, limit)]
                ListsCarryover[listname] = List.slice(limit)
              }
            }
          } else {
            /*
             * If (listname === 'name'), then unlimited - show all!
             * Then show ALL current content, then mix in all past content:
             */
            ListVisible = List
            /*
             * Mix in previous
             */
            if (show_one_list) {
              // Mix in Top
              if (ListTop.length) {
                ListVisible = [...ListTop, ...ListVisible]
              }
              // Mix in Carryover
              for (let lname in ListsCarryover) {
                let llist = ListsCarryover[lname]
                if (llist.length) {
                  let freq = lname === "com" ? 4 : lname === "tld" ? 5 : 6 //Math.min(Math.max(Math.floor(List.length / llist.length), 3), 9)
                  let start = lname === "phrase hack" ? 10 : 5
                  for (let i = 0; i < llist.length; i++) {
                    // every 3,4,5 items, insert a carryover item
                    // starting several items lower in the list, each time getting less lower
                    ListVisible.splice(i * freq + start, 0, llist[i])
                  }
                }
              }
            }
          }

          /*
           * more
           */
          let MoreLink = null
          if (List.length > ListVisible.length) {
            MoreLink = (
              <div className="dom_name unlimit">
                <span
                  onClick={() => {
                    this.setState({
                      ["unlimit_" + listname]: true
                    })
                  }}
                >
                  more &thinsp;
                  <FA className="fa-angle-down" icon={faCaretDown} />
                </span>
              </div>
            )
          } else if (this.state["unlimit_" + listname] === true) {
            MoreLink = (
              <div className="dom_name unlimit">
                <span
                  onClick={() => {
                    this.setState({
                      ["unlimit_" + listname]: false
                    })
                  }}
                >
                  less &thinsp;
                  <FA className="fa-angle-up" icon={faCaretUp} />
                </span>
              </div>
            )
          }

          /*
           * view
           */
          return ListVisible.length ? (
            <div key={li} className={"doms_group"}>
              <H5Styled className="doms_title">
                {show_one_list ? (!that.props.show_unavailable ? "Available Names" : "All Results") : label}
              </H5Styled>
              <div className="doms_content">
                {/*
                 * Results
                 */}
                {ListVisible.map((phraseString, pi) => {
                  if (phraseString[0] === "." || phraseString[1] === "." || phraseString[2] === ".") return null
                  return <Dom that={that} key={phraseString + pi} listname={listname} phraseString={phraseString} />
                })}
                {/*
                 * Link to "show more"
                 */}
                {MoreLink}
              </div>
            </div>
          ) : null
        })}
      </DomsStyled>
    )
  }
}

Doms.propTypes = {
  suggestions_domains: PropTypes.object,
  suggestions_availability: PropTypes.object
}

export default Doms
