import React from "react"
import PropTypes from "prop-types"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
import { faHeart, faSyncAlt, faDollarSign, faCalendarExclamation } from "@fortawesome/pro-regular-svg-icons"

const Dom = ({ that, phraseString, listname }) => {
  let { suggestions_availability, input_tld, input_str, input_spellchecked } = that.props
  let phraseStringNoSpaces = phraseString.replace(/ /g, "")
  let isExact =
    phraseStringNoSpaces === input_str.replace(/ /g, "") + "." + input_tld ||
    phraseStringNoSpaces === input_spellchecked.replace(/ /g, "") + " ." + input_tld
  let code = Number(suggestions_availability[phraseString.replace(/ /g, "")] || 0)
  //
  // unknown
  let status = "other"
  let Icon = <FA key={status} icon={faSyncAlt} />
  //
  // unknown
  if (code === 0) {
    status = "unknown"
    Icon = <span className="textQuestion">?</span>
  }
  // unavailable
  else if (code < 8) {
    status = "unavailable"
    Icon = <span className="textQuestion">-</span>
  }
  // error
  else if (code === 8) {
    status = "available"
    Icon = <span className="textQuestion">*</span>
  }
  // error
  else if (code === 9) {
    status = "available"
    Icon = <span className="textQuestion">?</span>
  }
  // available
  else if (code >= 10 && code <= 1000) {
    status = "available"
    Icon = <FA key={1} icon={faDollarSign} className="faDollarSign" />
  }
  // expiring
  else if (code > 100000000) {
    status = "expiring"
    Icon = <FA key={1} icon={faCalendarExclamation} />
  }
  // premium unknown
  else if (code === 10000000) {
    status = "premium"
    Icon = [
      <FA key={1} icon={faDollarSign} className="faDollarSign" />,
      <FA key={1} icon={faDollarSign} className="faDollarSign" />
    ]
  }
  // premium
  else if (code > 1000) {
    status = "premium"
    Icon = (
      <span>
        <FA key={1} icon={faDollarSign} className="faDoubleDollarSign" />
        <span className="price">{Math.floor(code)}</span>
      </span>
    )
  }
  let phraseArr = phraseString.split(" ")
  let singleLetter = phraseArr.find((str) => str.length === 1)
  return (
    <div className={`dom_name status-${status} ${isExact ? "status-exact" : ""}`}>
      <span className={"dom_card"}>
        <span className="icon">{Icon}</span>
        {(singleLetter && listname === "name") ||
        phraseArr.length > 5 ||
        listname === "tld" ||
        phraseArr.length > 3 ||
        listname === "com" ? (
          <span>
            {/*
             * remove spaces from long phrases in "tld" and "com" lists
             */}
            <span className="word">{phraseString.replace(/ /g, "")}</span>
          </span>
        ) : (
          <span>
            {/*
             * display spaces as kerning
             */}
            {phraseArr.map((str, i) => (
              <span key={str + i} className="word">
                {str}
              </span>
            ))}
          </span>
        )}
      </span>
    </div>
  )
}

Dom.propTypes = {
  that: PropTypes.object, // passed down from root container
  phraseString: PropTypes.string // domain string suggestion to show, with spaces
}

export default Dom
